
































































import { Component, Vue } from 'vue-property-decorator';
import CategoryList from "@/components/board/CategoryList.vue";
import MenuTitle from "@/components/board/MenuTItle.vue";
import {IBoard} from "@/components/board/interface/IBoard";
import {IComment} from "@/components/board/interface/IComment";


@Component({
  components: {
    CategoryList,
    MenuTitle
  },
})
export default class WriterForm extends Vue {

  c_content: string;
  newComment: string;
  isComment: boolean;

  comments: [{ c_content: string; updated_at: Date }];

  board: {
    title: string,
    content: string,
    writer: string,
  }

  constructor() {
    super();
    this.board = {
      title: '',
      content: '',
      writer: '',
    }
    this.comments = [
        {
      c_content: '',
      updated_at: new Date(),}
    ]

    // this.board.title = '';
    // this.board.writer = '';
    // this.board.content = '';
    //
    // this.comments._id = '';
    // this.comments.b_id = '';
    // this.comments.c_content = '';
    // this.comments.c_writer = '';
    // this.comments.created_at = new Date();
    // this.comments.updated_at = new Date();

    this.c_content = '';
    this.newComment = '';
    this.isComment = true;
  }

  async created() {
    const _id = this.$route.params.id;
    let { data } = await Vue.axios.get(`/board/d/${_id}`) as { data: any };
    this.board = data;

    console.log('---------------------------')

    let res = await Vue.axios.get(`/comment/${_id}`);
    console.log(res.data);
    if(!res.data){
      this.isComment = true;
    }
    this.comments = res.data;
    this.isComment = false;
    console.log('-------------------');
    console.log(this.comments);
    throw new Error();
  }

  async updateBoard(board: IBoard) {
    await this.$router.push({
      path: `/board/write/${board._id}`
    })
  }

  async deleteBoard(board: IBoard) {
    const { result } = await Vue.axios.delete(`/board/${board._id}`) as { result: boolean };
    if(result){
      await this.$router.push({
        path: '/board'
      })
    }
    throw new Error();
  }

  async cancelWrite() {
    await this.$router.push({
      path: '/board'
    })
  }

  //--  댓글입력
  async comment(){
    const _id = this.$route.params.id;
    const { result } = await Vue.axios.post('/comment',{
        c_content: this.c_content,
        c_writer: this.$store.state.memberName,
        b_id: _id,
    }) as { result: boolean };
    console.log(this.$store.state.memberName);
    if(result){
      this.c_content = '';
      location.reload();
    }

  }

  async delete_comment(comment: any){
    const _id = comment._id;
    const { result } = await Vue.axios.delete(`/comment/${_id}`) as { result: boolean };
    if(result){
      location.reload();
    } else {
      alert('삭제 실패');
    }
  }
  //수정 < - > 저장 버튼 전환
  async toggleBtn(event: any,comment: any,index: number){
    const ele = event.target;
    ele.classList.toggle('isUpdate');

    const isUpdate = ele.classList.contains('isUpdate');
    if(isUpdate){
      ele.innerText = '저장'
      document.getElementsByName('showComment')[index].removeAttribute('readonly');
    }else{
      ele.innerText = '수정'
      await this.saveComment(comment,index);
    }
  }

  async saveComment(comment: any ,index: number){
    const _id = comment._id;
    let data = ''
    //const data = document.getElementsByName('showComment')[index].value;
    comment.c_content = data as string;

    const { result } = await Vue.axios.patch(`/comment/${_id}`,comment) as { result: boolean };
    if(result){
      location.reload();
    }
  }

}// VUE END!!
